import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from "components/CustomButtons/Button.js";
import Typography from '@material-ui/core/Typography';
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import { MainContext } from "context/mainContext.js";

import AddShoppingCartIcon from '@material-ui/icons/AddShoppingCart';
import VisibilityOutlinedIcon from '@material-ui/icons/VisibilityOutlined';

import Fade from 'react-reveal/Fade';






const useStyles = makeStyles({
  root: {
    width: 300,
    height: "40vh",
    margin: 10,
    background: "#eeeeee",
    border: "1px solid #dddddd",
    display: "block", 
    textAlign: "center"
  },
  media: {
    height: "20vh",
    background: "#6C347C"
  },
  typo: {
    fontFamily:"Bebas neue",
    marginTop: -10,
    fontSize: 30
  },
  buttonsContainer: {
    width: "100%",
    bottom: 0,
    textAlign: "center",
  },
  buttons: {
    marginTop: -20
  },
});

export default function CourseCardMini(props) {
  const classes = useStyles();

  const context = useContext(MainContext)

  const { setSelectedCourse } = context;
  return (
    <Fade bottom>
    <Link to="/curso"><Card className={classes.root} onClick={() => {(setSelectedCourse(props.id))}} raised={true}>
    
      <CardActionArea>
        <CardMedia
          className={classes.media}
          image={props.img}
        />
        <CardContent>
          <Typography className={classes.typo} gutterBottom variant="h3" component="h3">
          {props.title}
          </Typography>

        </CardContent>
      </CardActionArea>
      <CardActions className={classes.buttons}>
        <GridContainer className={classes.buttonsContainer}>
          <GridItem xs={2}><Button justIcon round color="white" style={{border: "1px solid #39B54A"}}><AddShoppingCartIcon style={{color: "#39B54A"}}/></Button></GridItem>
          <GridItem xs={8}><Button round color="white" fullWidth="true" style={{border: "1px solid #39B54A"}}>
         <div style={{color: "#39B54A"}}>COMPRAR</div> 
        </Button></GridItem>
        <GridItem xs={1}><Link to="/curso"><Button justIcon round color="white"><VisibilityOutlinedIcon style={{color: "#6C347C"}}/></Button></Link></GridItem>
      
        
        
      </GridContainer></CardActions>
    </Card></Link></Fade>
  );
}