
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Button from "components/CustomButtons/Button.js";
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';




const useStyles = makeStyles({
  list: {
    width: "30vh",
    float: "left"
  },
  fullList: {
    width: 'auto',
    justifyContent: "left",
    alignItems: "left"
  },
  menuItem: {
    color: "#FF0055",
    padding: 10,
    borderBottomWidth: 1,
    borderBottomStyle: "solid",
    borderBottomColor: "#aaaaaa",
    width: "100%",
    display: "block"
  },
  nested: {
    paddingLeft: 10,
  },
});


export default function SwipeableTemporaryDrawer() {

  const [open, setOpen] = React.useState(false);

  const handleClick = () => {
    setOpen(!open);
  };

  const classes = useStyles();
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const [response, setData] = useState({ data: [] });

  useEffect(() => {
    const fetchData = async () => {
      const result = await axios(
        'http://apiacademy.dagnett.com/api/category/tree/0',
      );
 
      setData(result);
    };
 
    fetchData();
  }, []);

  console.log("data: ", response)

  const toggleDrawer = (anchor, open) => (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      //onClick={toggleDrawer(anchor, false)}
      //onKeyDown={toggleDrawer(anchor, false)}
    >
      <List
      component="nav"
      aria-labelledby="nested-list-subheader"
      subheader={
        
        <ListSubheader onClick={toggleDrawer(anchor, false)} component="div" id="nested-list-subheader">
         <ArrowBackIcon />
        </ListSubheader>
      }
      className={classes.root}
    >
    {response.data.map(item => (
          <ListItem className={classes.menuItem} button key={item.id} onClick={handleClick}>
            <ListItemText primary={item.name} />
        {item.childrens != null ? (<Collapse in={open} timeout="auto" unmountOnExit><List>{item.childrens.map(i => {return <ListItem className={classes.nested}><ListItemText primary={i.name} /></ListItem>})}</List></Collapse>) : ""}
      </ListItem>
      
        ))} 

    </List>
    </div>
  );

  return (
    <div>
      {['left'].map((anchor) => (
        <React.Fragment key={anchor}>
          <Button round variant="contained" color="primary" onClick={toggleDrawer(anchor, true)}>Todas las categoras</Button>
          <SwipeableDrawer
            variant="persistent"
            anchor={anchor}
            open={state[anchor]}
            onClose={toggleDrawer(anchor, false)}
            onOpen={toggleDrawer(anchor, true)}
          >
            {list(anchor)}
          </SwipeableDrawer>
        </React.Fragment>
      ))}
    </div>
  );
}