import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Button from "components/CustomButtons/Button.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import SearchIcon from '@material-ui/icons/Search';
import TextField from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';


import CancelIcon from '@material-ui/icons/Cancel';

import DrawerTabs from "../DrawerMenu/DrawerTabs";

const useStyles = makeStyles({
    list: {
        width: "100%",
    },
    fullList: {
        width: 'auto',
    },
    SearchDrawer: {
        height: "99vh",
        padding: 10,
        overflow: "hidden",
        background: "rgb(60, 37, 59)"
    },
    searchButton: {
        width: "30vh",

    },
    searchBar: {
        background: "#d7afe2",
        borderRadius: 20,
        marginTop: 20,
        marginBottom: -40

    },
    catTitle: {
        position: "absolute",
        top: "28%",
        color: "#d7afe24a",
        left: -40,
        fontSize: 35,
        transform: "rotate(-90deg)"
    }
});

export default function SearchDrawer() {
    const classes = useStyles();
    const [state, setState] = React.useState({
        top: false,
        left: false,
        bottom: false,
        right: false,
    });

    const toggleDrawer = (anchor, open) => (event) => {
        if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }

        setState({ ...state, [anchor]: open });
    };

    const list = (anchor) => (
        <div
            className={clsx(classes.list, {
                [classes.fullList]: anchor === 'top' || anchor === 'bottom',
            })}
            role="presentation"
        >
            <div className={classes.SearchDrawer}>

                <GridContainer direction="row"
                    justify="center"
                    alignItems="center"
                >
                    

                    <GridItem xs={12} sm={11}>
                        <form><TextField
                            
                            className={classes.searchBar}
                            id="input-with-icon-textfield"
                            variant="outlined"
                            fullWidth="true"
                            color="success"
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position="start">
                                        <SearchIcon />
                                    </InputAdornment>
                                ),
                            }}
                            

                        /></form></GridItem>

                        <GridItem xs={12} sm={1}><Button color="transparent" justIcon round onClick={toggleDrawer(anchor, false)} onKeyDown={toggleDrawer(anchor, false)}><CancelIcon color="white" variant="large"/></Button></GridItem>
                </GridContainer>
                <div className="hideOnMobile">
                    {/* <div className={classes.catTitle}>CATEGORIAS</div> */}
                {/* <DrawerTabs /> */}
                </div>


            </div>

        </div>
    );

    return (
        <div>
            {['top'].map((anchor) => (
                <React.Fragment key={anchor}>
                    <Button startIcon={<SearchIcon />} className={classes.searchButton} color="white" round onClick={toggleDrawer(anchor, true)}>Buscar</Button>
                    <SwipeableDrawer
                        anchor={anchor}
                        open={state[anchor]}
                        onClose={toggleDrawer(anchor, false)}
                        onOpen={toggleDrawer(anchor, true)}
                        variant="persistent"
                    >
                        {list(anchor)}
                    </SwipeableDrawer>
                </React.Fragment>
            ))}
        </div>
    );
}