import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
//import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from "components/CustomButtons/Button.js";
import Typography from '@material-ui/core/Typography';
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import { MainContext } from "context/mainContext.js";

import AddShoppingCartIcon from '@material-ui/icons/AddShoppingCart';
import VisibilityOutlinedIcon from '@material-ui/icons/VisibilityOutlined';

import Slide from 'react-reveal/Slide';






const useStyles = makeStyles({
  root: {
    width: 300,
    height: "50vh",
    margin: 10,
    cursor:"grabbing",
    background: "#eeeeee"
  },
  media: {
    height: "30vh",
    cursor:"grabbing"
  },
  typo: {
    fontFamily:"Bebas neue",
    marginTop: -10,
    fontSize: 30
  },
  buttonsContainer: {
    display: "flex",
    width: "100%",
    justifyContent: "center",
    marginTop: -25
  },
  buttons: {
    marginTop: 2
  },
});

export default function CourseCard(props) {
  const classes = useStyles();

  const context = useContext(MainContext)

  const { setSelectedCourse } = context;
  return (
    <Slide right>
    <Card className={classes.root} onClick={() => {(setSelectedCourse(props.id))}} raised={true}>
    

        <CardMedia
          className={classes.media}
          image={props.img}
        />
        <CardContent>
          <Typography>{props.type}</Typography>
          <Typography className={classes.typo} gutterBottom variant="h3" component="h3">
          {props.title}
          </Typography>

        </CardContent>

      <CardActions>
        <GridContainer className={classes.buttonsContainer}>
          <GridItem xs={2}><Button justIcon round color="success"><AddShoppingCartIcon style={{color: "#FFFFFF"}}/></Button></GridItem>
          <GridItem xs={8}><Button round color="success" fullWidth="true">
          COMPRAR
        </Button></GridItem>
        <GridItem xs={2}><Link to="/curso"><Button justIcon round color="primary"><VisibilityOutlinedIcon style={{color: "#FFFFFF"}}/></Button></Link></GridItem>
      
        
        
      </GridContainer></CardActions>
    </Card></Slide>
  );
}