import React, { useContext, useState, useEffect } from 'react';
import axios from 'axios';
import PropTypes from 'prop-types';
import { MainContext } from "context/mainContext.js";
import { makeStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';


import Header from "components/Header/Header.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Parallax from "components/Parallax/Parallax.js";
import Logo from "../../components/Logo/Logo";
import CourseCardMini from 'views/LandingPage/Sections/CourseCardMini';
import { Accordion, AccordionItem } from 'react-sanfona';




function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    display: 'flex',
    minHeight: "73vh"
  },
  tabs: {
    borderRight: `1px solid ${theme.palette.divider}`,
  },
  title: {
      fontFamily: "Bebas neue",
      fontSize: 45,
      marginLeft: 20
  },
  menuItem: {
    fontSize: 30,
    fontFamily: "Bebas neue",
    marginLeft: 20,
  },
  subMenuItem: {
    fontSize: 22,
    fontFamily: "Bebas neue",
    marginLeft: 10,
  },
  menuContainer: {
    minWidth: "100%",
    padding: 10,
    textAlign: "left",
    borderRight: `2px solid ${theme.palette.divider}`,
  }
}));

export default function VerticalTabs(props) {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const { ...rest } = props;

  const context = useContext(MainContext)
  const { setSelectedCat } = context;

  //axios data

  const [response, setData] = useState({ data: [] });
  // eslint-disable-next-line
  const [responseCat, setDataCat] = useState({ dataCat: [] });

  // eslint-disable-next-line
  useEffect(() => {
    const fetchData = async () => {
      const result = await axios(
        'http://apiacademy.dagnett.com/api/category/tree/0',
      );
 
      setData(result);
    };
 
    fetchData();

    const fetchDataCat = async () => {
      const resultCat = await axios(
        "http://apiacademy.dagnett.com/api/category/"+context.selectedCat+"/course",
      );
 
      setDataCat(resultCat);
    };
 
    fetchDataCat();
  }, [context.selectedCat]);


  const handleChange = (event, newValue) => {
    setValue(0);
  };

  console.log("dataCat",responseCat.data)



  return (
      <div>
       <div><Header
        color="transparent"
        brand=<Logo />
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 10,
          color: "white",
        }}
        {...rest}
      />

    <Parallax small filter image={require("assets/img/userPortada.png")} style={{height:"15vh"}}/><h1 className={classes.title}>Qu deseas aprender?</h1></div>
     
    <div className={classes.root}>
        
   <div style={{display: "block"}}>
    <Accordion>
        {response.data.map(item => {
          return (
           
            <AccordionItem className={classes.menuContainer} titleClassName={classes.menuItem} onExpand={() => {(setSelectedCat(item.id))}} title={`${item.name}`} expanded={item === 1}>
              <div>
              {response.data.map(item => {
            var child = item.childrens;
            var childRes = child != null ? child.map(i => (i.parent_id != context.selectedCat ? "" : <Accordion><AccordionItem className={classes.menuContainer} onExpand={() => {(setSelectedCat(i.id))}} titleClassName={classes.subMenuItem} title={`${i.name}`} expanded={i === 1}></AccordionItem></Accordion>)) : console.log("0");
           return childRes;
        })}
              </div>
            </AccordionItem>
          );
        })}
      </Accordion></div>
      
        <div style={{ display: "grid", gridTemplateColumns: "0fr 0fr 0fr", width: "100%", paddingLeft: 50}}>
          {responseCat.data != null ? responseCat.data.map(item =>  <CourseCardMini id={item.id} backColor="#fff" title={item.name} rating="4.8" profesor="Christian Flores" img={item.picture} />)
            : ""}

        </div>
            

      
    </div>
    
  </div>);
}