import React from "react";
import { Link } from 'react-router-dom';
import customStyles from "../../../assets/css/styles.css";
// material-ui components

// @material-ui/icons

import Face from "@material-ui/icons/Face";
import Chat from "@material-ui/icons/Chat";
import Build from "@material-ui/icons/Build";
import VpnKeyIcon from '@material-ui/icons/VpnKey';
import FacebookIcon from '@material-ui/icons/Facebook';
// core components
import CustomTabs from "components/CustomTabs/CustomTabs.js";
import InputAdornment from "@material-ui/core/InputAdornment";
// @material-ui/icons
import LockIcon from '@material-ui/icons/Lock';
import PermIdentityIcon from '@material-ui/icons/PermIdentity';
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import { Button } from "@material-ui/core";





export default function LoginRegisterTabs() {
  
  return (
    <div className={customStyles.modalBack}>
      <CustomTabs
        headerColor="success"
        tabs={[
          {
            tabName: "Ingresa a tu Academia",
            tabIcon: Face,
            tabContent: (
              <GridContainer style={{ width: 550, minHeight: 320 }}>
                <Button variant="outlined" color="success" endIcon={<FacebookIcon />}>
                  Ingresar con Facebook
                </Button>
                <Button variant="outlined" color="success">
                  Ingresar con Google
                </Button>
                <GridItem xs={12} sm={12} md={10}>
                  <CustomInput
                    labelText="Ingresa tu correo"
                    id="login.email"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      endAdornment: (<InputAdornment position="end"><PermIdentityIcon /></InputAdornment>)
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={10}>
                  <CustomInput
                    labelText="Ingresa tu contrasea"
                    id="login.password"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      endAdornment: (<InputAdornment position="end"><LockIcon /></InputAdornment>)
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={12}>
                  <Button  variant="outlined" color="success" endIcon={<VpnKeyIcon />}>
                  <Link to="/profile-page">
    Ingresar
  </Link>
</Button>
                </GridItem>

              </GridContainer>
            )
          },
          {
            tabName: "Registrarse",
            tabIcon: Chat,
            tabContent: (
              <GridContainer style={{ width: 500, minHeight: 320 }}>
                <Button variant="outlined" color="success" endIcon={<FacebookIcon />}>
                  Registrar con Facebook
                </Button>
                <Button variant="outlined" color="success">
                  Registar con Google
                </Button>
                <GridItem xs={12} sm={12} md={10}>
                  <CustomInput
                    labelText="Ingresa tu nombre completo"
                    id="login.email"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      endAdornment: (<InputAdornment position="end"><PermIdentityIcon /></InputAdornment>)
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={10}>
                  <CustomInput
                    labelText="Ingresa tu correo"
                    id="login.email"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      endAdornment: (<InputAdornment position="end"><PermIdentityIcon /></InputAdornment>)
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={10}>
                  <CustomInput
                    labelText="Ingresa tu contrasea"
                    id="login.password"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      endAdornment: (<InputAdornment position="end"><LockIcon /></InputAdornment>)
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={12}>
                  <Button variant="outlined" color="success" endIcon={<VpnKeyIcon />}>
                    Crear cuenta
</Button>
                </GridItem>

              </GridContainer>
            )
          },
          {
            tabName: "Recuperar contrasea",
            tabIcon: Build,
            tabContent: (
              <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                  <CustomInput
                    labelText="Ingresa el correo de tu cuenta para recuperar"
                    id="login.email"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      endAdornment: (<InputAdornment position="end"><PermIdentityIcon /></InputAdornment>)
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={12}>
                  <Button variant="outlined" color="success" endIcon={<VpnKeyIcon />}>
                    Recuperar
</Button>
                </GridItem>

              </GridContainer>
            )
          }
        ]}
      />
    </div>
  );
};